$maxWidth:1280;
@mixin adaptiv-font($pcSize, $mobSize) {
	$addSize: $pcSize - $mobSize;
	$addMobSize: $addSize + $addSize * 0.7;
	@media (max-width: 767px) {
		font-size: calc(#{$mobSize + px} + #{$addMobSize} * ((100vw - 320px) / #{$maxWidth}));
	}
	@media (min-width: 767px) {
		font-size: calc(#{$mobSize + px} + #{$addSize} * (100vw / #{$maxWidth}));
	}
}



@media (max-width:600px) {
  .toDoItem{
    max-width: 350px;
  }
  div{
    max-width: 500px;
  }
  p{
    max-width: 250px;
  }
}

@media (max-width:570px) {
  .toDoItem{
    max-width: 300px;
  }
  div{
    max-width: 500px;
  }
  p{
    max-width: 250px;
  }
}

@media (max-width:370px) {
  .toDoItem{
    max-width: 280px;
  }
  div{
    max-width: 350px;
  }
  p{
    max-width: 250px;
  }
}
@media (max-width:320px) {
  .toDoItem{
    max-width: 180px;
  }
  div{
    max-width: 250px;
  }
  p{
    max-width: 90px;
  }
}


.settingsButton{
  background-color: transparent;
  border: none;
  width: 25px;
}

button{
    width: 100px;
    padding: 6px 0 6px;
    position: relative;
    margin: 8px;
    left: 0px;
    bottom: 5px;
    background-color: rgb(0, 0, 0);
    color: white;
    border: 2px solid rgb(0, 0, 0);
    cursor: pointer;
    border-radius: 13px;
    transition: 0.3s all ease-in-out;
    
}

div{
    margin: 20px;
    color: white;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    min-height: 100%;
  }
 


  .toDoItem{
    text-align: center;
    margin: 20px;
    max-height: 450px;
    background-color: rgb(50, 50, 50);
    border-radius: 20px;
    min-height: 110px;
    width: 400px;

    p{
        font-size: 22px;
        position: relative;
        top: 10px;
        max-width: 400px;
        cursor: pointer;
        // &:hover{
        //   color: rgb(151, 0, 0);
        // }
    }
  }
 